#game2-wrapper,
#game2-content,
#room-itself {
  width: 100%;
  height: 100%;
  // font-size: 1.6em;
}

#floating-config-btn {
  position: absolute;
  top: 0;
  right: 0;
  padding: 10px 10px 20px 20px;
  width: auto;
  height: auto;
  background-color: #f9afa1;
  border-radius: 45% 0 15% 100%;
  transition: 250ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
  z-index: 10;
}

#floating-config-btn img {
  transition: 100ms linear;
}

#floating-config-btn:hover {
  cursor: pointer;
  padding: 10px 10px 30px 30px;
  border-radius: 0 0 0 90%;
}

#floating-config-btn:hover img {
  filter: saturate(0%) brightness(999%);
}

#charData {
  margin-top: 100px;
}

#tips-notepad:hover {
  height: 15em;
  filter: drop-shadow(8px 4px 0px var(--color-fourth));
}

#big-note-wrapper {
  animation: slideIn 400ms cubic-bezier(0, 0, 0, 1) 150ms forwards;
  transform: translateY(100vh);
  position: relative;
  // --hSize: 80vh;
  --hSize: calc(var(--gameHeight) * 0.8);
  height: var(--hSize);
  width: calc(9 / 10 * var(--hSize));
  margin: calc(calc(var(--gameHeight) - var(--hSize)) / 2) auto;
}

#big-note-imgs {
  position: absolute;
  height: 100%;
  width: 100%;
  transform-origin: 68% 49.1%;
  transform: scale(1.24);
}

#big-note-imgs > img {
  position: absolute;
  width: 100%;
  height: 100%;
}

#big-note-writings-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 12em 7em 0em 19em;
  overflow: hidden;
}

#big-note-writings-content {
  display: flex;
  flex-direction: column;
  align-content: stretch;
  justify-content: flex-start;
  font-size: 2.5em;

  span {
    padding: 0.25em 1em 0.1em;
    margin: 0.25em 0em 0.1em;
    border-bottom: #ccc 0.15em solid;
  }

  #note-dicas {
    font-family: "Abril FatFace";
    font-size: 2em;
    border-bottom-width: 0.075em;
  }
}
