@keyframes openOverlay {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes closeOverlay {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

#fullscreen-overlay {
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #ffffffbb;
  background-color: rgba(var(--bgR), var(--bgG), var(--bgB), var(--bgA));
  overflow: hidden;
  opacity: 1;
  pointer-events: all;
  animation: openOverlay 250ms linear forwards;
}

#fullscreen-overlay.ExitAnim {
  animation: closeOverlay 250ms linear forwards;
}

#fullscreen-overlay-close-btn {
  position: absolute;
  cursor: pointer;
  right: 1em;
  top: 1em;
  width: 2.5em;
  height: 2.5em;
  font-size: 6em;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-bottom: 15px;
  transition: 100ms ease-in-out;
  overflow: hidden;
  color: #59316d;
  color: rgba(var(--closeR), var(--closeG), var(--closeB), 1);
}

#fullscreen-overlay-close-btn:hover {
  color: #f9afa1;
  color: rgba(var(--closeHoverR), var(--closeHoverG), var(--closeHoverB), 1);
  transform: scale(1.2);
}
