@import "./consts";
@import "./colorPalettes";
@import "./defaults";

.App {
  display: flex;
  flex-direction: column;
}

.App.game-mode {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
}

#main-app-wraper {
  flex-grow: 1;
}

.App.game-mode #main-app-wraper {
  flex-grow: 0;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

